import { SignEditor, HTapeLayout, Searcher } from '@fui/core';
import { shortcut } from '@engine/core';
import { ParameterMultiSelectPopup } from './multiselectpopup';
import { ParameterMultiSelectSearchPopup } from './multiselectsearchpopup';

@shortcut()
export class ParameterMultiSelectTrigger extends BI.Trigger {
    public static EVENT_SEARCHING = 'EVENT_SEARCHING';
    public static EVENT_STOP = 'EVENT_STOP';
    public static EVENT_START = 'EVENT_START';
    public static EVENT_CHANGE = 'EVENT_CHANGE';
    public static EVENT_FOCUS = 'EVENT_FOCUS';
    public static EVENT_BLUR = 'EVENT_BLUR';
    public static EVENT_CONFIRM = 'EVENT_CONFIRM';
    public static EVENT_SELECT_ALL = 'EVENT_SELECT_ALL';
    public static EVENT_CLEAR = 'EVENT_CLEAR';
    public static xtype = 'report.search_text_value_trigger';
    public static EVENT_CLICK = 'EVENT_CLICK';

    public props = {
        baseCls: 'bi-search-text-value-trigger bi-border bi-focus-shadow',
        height: 24,
        itemsCreator: BI.emptyFn as Function,
        valueGetter: () => [] as {text: string; value: string}[],
        unmatchedValueGetter: () => [] as {text: string; value: string}[],
        watermark: '',
        allowEdit: false,
        itemHeightGetter: () => 24,
        customData: true,
        value: [] as {text: string;value: string;}[],
    }

    private searcher: Searcher;

    private editor: SignEditor;

    private popup: ParameterMultiSelectSearchPopup;

    private wrapper: HTapeLayout;

    public render() {
        const { height, itemsCreator, valueGetter, watermark, unmatchedValueGetter, customData, itemHeightGetter } = this.options;

        return {
            type: BI.HTapeLayout.xtype,
            ref: (_ref: HTapeLayout) => {
                this.wrapper = _ref;
            },
            items: [
                {
                    el: {
                        type: BI.Searcher.xtype,
                        ref: (_ref: Searcher) => {
                            this.searcher = _ref;
                        },
                        isAutoSearch: false,
                        el: {
                            type: BI.SignEditor.xtype,
                            ref: (_ref: SignEditor) => {
                                this.editor = _ref;
                            },
                            value: this.joinValues(valueGetter()),
                            allowBlank: true,
                            watermark,
                            height,
                            listeners: [{
                                eventName: BI.SignEditor.EVENT_FOCUS,
                                action: () => {
                                    this.editor.setValue('');
                                    this.fireEvent(ParameterMultiSelectTrigger.EVENT_FOCUS);
                                },
                            }, {
                                eventName: BI.SignEditor.EVENT_BLUR,
                                action: () => {
                                    this.fireEvent(ParameterMultiSelectTrigger.EVENT_BLUR);
                                },
                            }, {
                                eventName: BI.SignEditor.EVENT_CLICK,
                                action: () => {
                                    this.fireEvent(ParameterMultiSelectTrigger.EVENT_CLICK);
                                },
                            }],
                        },
                        popup: {
                            type: ParameterMultiSelectSearchPopup.xtype,
                            itemHeightGetter,
                            customData,
                            cls: 'bi-card',
                            ref: (_ref: ParameterMultiSelectSearchPopup) => {
                                this.popup = _ref;
                            },
                            keywordGetter: () => this.editor.getValue(),
                            unmatchedValueGetter,
                            itemsCreator,
                            valueGetter,
                            listeners: [{
                                eventName: ParameterMultiSelectPopup.EVENT_CHANGE,
                                action: (text: string, value: string, isSelected: boolean) => {
                                    this.fireEvent(ParameterMultiSelectTrigger.EVENT_CHANGE, text, value, isSelected);
                                },
                            }, {
                                eventName: ParameterMultiSelectPopup.EVENET_ADD_ITEM,
                                action: (text: string, value: string, isSelected: boolean) => {
                                    this.fireEvent(ParameterMultiSelectTrigger.EVENT_CHANGE, text, value, isSelected);
                                    this.searcher.stopSearch();
                                },
                            }, {
                                eventName: ParameterMultiSelectPopup.EVENT_CONFIRM,
                                action: () => {
                                    this.fireEvent(ParameterMultiSelectTrigger.EVENT_CONFIRM);
                                },
                            }, {
                                eventName: ParameterMultiSelectPopup.EVENT_CLEAR,
                                action: () => {
                                    this.fireEvent(ParameterMultiSelectTrigger.EVENT_CLEAR);
                                },
                            }, {
                                eventName: ParameterMultiSelectPopup.EVENT_SELECT_ALL,
                                action: (isSelected: boolean) => {
                                    this.fireEvent(ParameterMultiSelectTrigger.EVENT_SELECT_ALL, isSelected, this.popup.getAllLeaves());
                                },
                            }],
                        } as Partial<ParameterMultiSelectSearchPopup['props']>,
                        onSearch: (_op: { keyword: string }, callback: Function) => {
                            callback();
                        },
                        listeners: [{
                            eventName: BI.Searcher.EVENT_CHANGE,
                            action: () => {
                                
                            },
                        }, {
                            eventName: BI.Searcher.EVENT_START,
                            action: () => {
                                this.fireEvent(ParameterMultiSelectTrigger.EVENT_START);
                            },
                        }, {
                            eventName: BI.Searcher.EVENT_SEARCHING,
                            action: () => {
                                this.fireEvent(ParameterMultiSelectTrigger.EVENT_SEARCHING);
                            },
                        }, {
                            eventName: BI.Searcher.EVENT_STOP,
                            action: () => {
                                this.fireEvent(ParameterMultiSelectTrigger.EVENT_STOP);
                            },
                        }],
                    },
                },
                {
                    el: {
                        type: BI.Layout.xtype,
                    },
                    width: 0,
                },
                {
                    el: {
                        type: BI.Layout.xtype,
                        width: 24,
                    },
                    width: 24,
                },
            ],
        };
    }

    public mounted() {
        !this.options.allowEdit && BI.createWidget({
            type: BI.AbsoluteLayout.xtype,
            element: this,
            items: [{
                el: {
                    type: BI.Text.xtype,
                    title: () => this.editor.getValue(),
                },
                left: 0,
                right: 24,
                top: 0,
                bottom: 0,
            }],
        });
    }

    public focus() {
        this.editor.focus();
    }

    public blur() {
        this.editor.blur();
    }

    public setValue(vals: {text: string, value: string}[]) {
        this.editor.setValue(this.joinValues(vals));
    }

    public isSearching() {
        return this.searcher.isSearching();
    }

    public stopEditing() {
        this.searcher.stopSearch();
    }

    public getSearcher() {
        return this.searcher;
    }

    public getValue() {
        return this.popup.getValue();
    }

    public setAdapter(adapter: any) {
        this.searcher.setAdapter(adapter);
    }

    public adjustPopupHeight(adapter: any) {
        this.setAdapter(adapter);
        this.searcher.populate();
        this.searcher.adjustHeight();
    }

    private joinValues(vals: {text: string, value: string}[]) {
        return vals.map(val => val.text).join(',');
    }

    public refreshPlaceHolderWidth(width: number) {
        this.wrapper.attr('items')[1].width = width;
        this.wrapper.resize();
    }

    public setText(v: string) {
        this.editor.setValue(v);
    }

    public setWaterMark(v: string) {
        this.editor.setWaterMark(v);
    }
}
